

.stock_card{
  background-color: rgba(255, 255, 255, .5);
  height: 500px;
  border: 1px solid #e1e1e1;
  width: 350px;
  text-align: center;
  padding: 120px 50px 60px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
  cursor: pointer;

  &:hover {
    .title{
      h3{
        color: #cc2d18;
      }
    }

    .text{
      p{
       color: #cc2d18;
      }
    }
  }

  .back_img{
    object-fit: contain;
    position: absolute;
    bottom: 60px;
    z-index: -1;
    width: 100%;
  }
  @media (max-width: 1024px) {
    width: 290px;
  }
  @media (max-width: 768px) {
    width: 340px;
    height: 400px;
    padding: 90px 50px 50px;
  }
  @media (max-width: 425px) {
    width: 100%;
  }
  .title{
    h2{
      font-family: "Cormorant", serif;
      font-size: 32px;
      font-weight: 300;
      line-height: 32px;
      @media (max-width: 768px){
        margin-bottom: 20px;
      }
    }
  }
  .text{
    p{
      font-size: 15px;
      font-weight: 300;
      line-height: 22px;
      @media (max-width: 768px){
        margin-bottom: 20px;
      }
    }
  }
  .btn{
    button{
      background-color: #543019;
      color: #fff;
      font-size: 11px;
      font-weight: 400;
      text-transform: uppercase;
      padding: 10px 10px;
    }
  }
}