.actual {
  margin-bottom: 100px;
  .actual_banner {
    max-width: 1150px;
    height: 216px;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 50px 0 120px;
    cursor: pointer;
    @media (max-width: 430px) {
      padding: 10px;
      gap: 10px;
      flex-direction: column-reverse;
      justify-content: center;
    }

    h2 {
      max-width: 300px;
      margin-left: 90px;
      text-align: center;
      font-size: 30px;
      font-weight: 300;
      line-height: 32px;
      @media (max-width: 768px) {
        font-size: 24px;
      }
      @media (max-width: 430px) {
        margin-left: 0;
      }
    }

    p {
      max-width: 240px;
      font-weight: 400;
      font-size: 15px;
      line-height: 22px;
    }

    .actual_text {
      @media (max-width: 430px) {
        display: none;
      }
    }
  }

  .cards {
    display: flex;
    justify-content: space-between;
    @media (max-width: 768px) {
      height: 200px;
    }
    @media (max-width: 430px) {
      flex-wrap: wrap;
    }
    .cards_item:nth-last-child(1) {
      @media (max-width: 1150px) {
        display: none;
      }
    }

    .cards_item:nth-last-child(2) {
      @media (max-width: 425px) {
        display: none;
      }
    }
    .loader{
      margin: 100px auto 50px;
    }
  }
}

.more {
  text-align: center;
  margin-top: 30px;
  @media (max-width: 425px) {
    margin-top: 180px;
  }
  a {
    height: 60px;
    position: relative;
    display: inline-block;
    padding: 0 30px;
    background-color: #4D1200;
    font-family: Circe, sans-serif;
    font-size: 13px;
    font-weight: 400;
    letter-spacing: 1.86px;
    text-transform: uppercase;
    line-height: 60px;
    margin-top: 30px;
    color: #fff;
  }
}