html {
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
}

@font-face {
  font-family: "Blacker Display", sans-serif;
  src: url("./fonts/Blacker-Display-Medium-trial.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Blacker Display", sans-serif;
  src: url("./fonts/Blacker-Display-Bold-trial.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: "Blacker Display", sans-serif;
  src: url("./fonts/Blacker-Display-Light-trial.ttf") format("truetype");
  font-weight: 200;
  font-style: normal;
}
@font-face {
  font-family: "DS CenturyCapitals", sans-serif;
  src: url("./fonts/DS CenturyCapitals Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}

@import url('https://fonts.googleapis.com/css2?family=Cormorant&family=Great+Vibes&family=Moon+Dance&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,700;1,500;1,600&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul{
  list-style: none;
  padding: 0;
}

a{
  color: inherit;
  text-decoration: none;
}

p{
  margin-bottom: 0;
}

button{
  border: none;
  outline: none;
  cursor: pointer;
}

input{
  border: none;
  outline: none;
}

body{
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: 'Raleway', sans-serif;
  overflow-x: hidden;
}